"use client"

import { useEffect, useRef } from "react"

type Point = { x: number; y: number }

const lerp = (a: number, b: number, t: number) => a + (b - a) * t
const clamp = (n: number, min: number, max: number) => Math.max(min, Math.min(max, n))

export function MagneticCursor() {
  const cursorRef = useRef<HTMLDivElement>(null)
  const dotRef = useRef<HTMLDivElement>(null)

  const mouse = useRef<Point>({ x: 0, y: 0 })
  const pos = useRef<Point>({ x: 0, y: 0 })
  const target = useRef<Point>({ x: 0, y: 0 })

  const hoveredEl = useRef<HTMLElement | null>(null)
  const isHoveringMagnetic = useRef(false)

  useEffect(() => {
    const cursor = cursorRef.current
    const dot = dotRef.current
    if (!cursor || !dot) return

    let raf = 0
    let last = performance.now()

    const onMove = (e: MouseEvent) => {
      mouse.current.x = e.clientX
      mouse.current.y = e.clientY
    }

    const onOver = (e: MouseEvent) => {
      const el = (e.target as HTMLElement | null)?.closest?.("[data-magnetic]") as HTMLElement | null
      hoveredEl.current = el
      isHoveringMagnetic.current = !!el

      if (el) {
        cursor.classList.add("scale-150")
        dot.classList.add("scale-0")
      }
    }

    const onOut = (e: MouseEvent) => {
      const related = e.relatedTarget as HTMLElement | null
      if (related?.closest?.("[data-magnetic]")) return

      hoveredEl.current = null
      isHoveringMagnetic.current = false

      cursor.classList.remove("scale-150")
      dot.classList.remove("scale-0")
    }

    const animate = (t: number) => {
      const dt = Math.min((t - last) / 1000, 0.033)
      last = t

      // base target is the mouse
      target.current.x = mouse.current.x
      target.current.y = mouse.current.y

      // magnetic pull toward element center
      if (isHoveringMagnetic.current && hoveredEl.current) {
        const rect = hoveredEl.current.getBoundingClientRect()
        const cx = rect.left + rect.width / 2
        const cy = rect.top + rect.height / 2

        // pull strength: 0..1 (bigger = stickier)
        const strength = 0.35

        // also use mouse distance from center to create a "tension" feel
        const dx = mouse.current.x - cx
        const dy = mouse.current.y - cy
        const max = Math.max(rect.width, rect.height) * 0.75
        const dist = Math.hypot(dx, dy)
        const falloff = 1 - clamp(dist / max, 0, 1)

        target.current.x = lerp(mouse.current.x, cx, strength * falloff)
        target.current.y = lerp(mouse.current.y, cy, strength * falloff)

        // subtle element nudge (optional but nice)
        const nudge = 10 * falloff
        hoveredEl.current.style.transform = `translate(${dx * 0.06}px, ${dy * 0.06}px)`
        hoveredEl.current.style.transition = "transform 120ms ease-out"
        hoveredEl.current.style.willChange = "transform"
        hoveredEl.current.style.setProperty("--magnetic-nudge", `${nudge}px`)
      }

      // smooth cursor follow (time-based-ish)
      const follow = 1 - Math.pow(0.001, dt) // stable across FPS
      pos.current.x = lerp(pos.current.x, target.current.x, follow)
      pos.current.y = lerp(pos.current.y, target.current.y, follow)

      cursor.style.transform = `translate3d(${pos.current.x}px, ${pos.current.y}px, 0) translate(-50%, -50%)`
      dot.style.transform = `translate3d(${mouse.current.x}px, ${mouse.current.y}px, 0) translate(-50%, -50%)`

      raf = requestAnimationFrame(animate)
    }

    window.addEventListener("mousemove", onMove, { passive: true })
    document.addEventListener("mouseover", onOver, { passive: true })
    document.addEventListener("mouseout", onOut, { passive: true })

    // init to center
    mouse.current = { x: window.innerWidth / 2, y: window.innerHeight / 2 }
    pos.current = { ...mouse.current }
    target.current = { ...mouse.current }

    raf = requestAnimationFrame(animate)

    return () => {
      cancelAnimationFrame(raf)
      window.removeEventListener("mousemove", onMove)
      document.removeEventListener("mouseover", onOver)
      document.removeEventListener("mouseout", onOut)

      // clean any leftover transform on hovered element
      if (hoveredEl.current) hoveredEl.current.style.transform = ""
    }
  }, [])

  return (
    <>
      {/* Outer ring */}
      <div
        ref={cursorRef}
        className="fixed left-0 top-0 z-[9999] pointer-events-none h-8 w-8 -translate-x-1/2 -translate-y-1/2 rounded-full border border-black/80 transition-transform duration-150"
        style={{ mixBlendMode: "difference" }}
      />
      {/* Inner dot */}
      <div
        ref={dotRef}
        className="fixed left-0 top-0 z-[9999] pointer-events-none h-2 w-2 -translate-x-1/2 -translate-y-1/2 rounded-full bg-black/90 transition-transform duration-150"
        style={{ mixBlendMode: "difference" }}
      />
    </>
  )
}
